import React, { RefCallback, useCallback, memo, useContext, useEffect } from "react";
import { useSpring, animated, config } from "react-spring";
import { useDrag, useDrop } from "react-dnd";
import { MantineColor } from "@mantine/core";
import { VirtualItem } from "react-virtual";

import { ACTION_TYPE } from "../DnDGridProvider/model";
import { DnDGridDispatch } from "../DnDGridProvider";
import { CELL, ITEM_TYPE } from "./consts";
import { CellModel } from "./model";
import StyledDnDCell from "./styles";

interface Props extends CellModel {
    virtualRow: VirtualItem;
    virtualCol: VirtualItem;
    color?: MantineColor;
}

const DnDCell: React.FC<Props> = memo(({ id, label, color, virtualRow, virtualCol }) => {
    const dispatch = useContext(DnDGridDispatch);

    const styles = useSpring({
        delay: (virtualRow.index + virtualCol.index) * 50,
        config: config.wobbly,
        from: { scale: 0 },
        to: { scale: 1 },
    });

    const [{ isDragging }, connectDrag] = useDrag(() => ({
        item: { id },
        type: ITEM_TYPE.CELL,
        collect: (monitor) => {
            return {
                isDragging: monitor.isDragging(),
            };
        },
    }));

    const [{ isOver }, connectDrop] = useDrop<CellModel, unknown, { isOver: boolean }>(() => ({
        accept: ITEM_TYPE.CELL,
        drop: (droppedCell) => {
            if (droppedCell.id !== id) {
                dispatch({
                    type: ACTION_TYPE.SWAP_CELL,
                    payload: {
                        source: droppedCell.id,
                        target: id,
                    },
                });
            }
        },
        collect: (monitor) => {
            return {
                isOver: monitor.isOver(),
            };
        },
    }));

    const combineRefs = useCallback<RefCallback<HTMLDivElement>>(
        (element) => {
            connectDrag(element);
            connectDrop(element);
        },
        [connectDrag, connectDrop]
    );

    return (
        <animated.div
            id={id}
            style={{
                top: 0,
                left: 0,
                padding: CELL.MARGIN,
                position: "absolute",
                width: `${virtualCol.size}px`,
                height: `${virtualRow.size}px`,
                transform: `translateX(${virtualCol.start}px) translateY(${virtualRow.start}px)`,
                ...styles,
            }}
        >
            <StyledDnDCell
                color={color}
                isOver={isOver}
                isDragging={isDragging}
                textLength={label.length}
                ref={combineRefs}
            >
                {label}
            </StyledDnDCell>
        </animated.div>
    );
});

DnDCell.displayName = "DnDCell";
export default DnDCell;
